<template>
  <div class="shop">
    <h1>Hello {{(currUser) ? currUser.nickName : 'Guest'}}</h1>
    <router-link to="/shop/toy/ToyEdit" v-if="currUser.isAdmin">Add</router-link>
    <ToyFilter @input="filterList"/>
    <ToysList :toys="toys" :currUser="currUser" @removeToy="removeToy"/>
  </div>
</template>

<script>
// @ is an alias to /src
import ToysList from "@/components/ToysList.vue";
import ToyFilter from "@/components/ToyFilter.vue";

export default {
  name: "shop",
  components: {
    ToysList,
    ToyFilter
  },
  created() {
    this.$store.dispatch({ type: "loadToys" });
  },
  computed: {
    toys() {
      console.log(this.$store.getters.toyList);
      return this.$store.getters.toyList;
    },
    currUser(){
      return this.$store.getters.currUser
    }
  },
  methods: {
    removeToy(toyId) {
      this.$store.dispatch({ type: "removeToy", toyId });
    },
    filterList(filterBy){
      console.log(filterBy);
      this.$store.dispatch({ type: "filterToys", filterBy });
    }
  }
};
</script>

