import axios from 'axios'

 const BASE_URL = (process.env.NODE_ENV !== 'development') ? '/toy'  : '//localhost:3003/toy';



export default {
    query,
    getEmpty,
    getById,
    saveToy,
    removeToy
}


function removeToy(toyId) {
    return axios.delete(`${BASE_URL}/${toyId}`)
        .then(res => res)
}

function saveToy(toy){
    if (toy._id) {
        console.log('EDIT',toy);
        return axios.put(`${BASE_URL}/${toy._id}`,toy)
        .then(res => {
            console.log(res.data);
            return res.data
        } )
    } else {
        console.log('ADD',toy);
        return axios.post(`${BASE_URL}`,toy)
        .then(res => res.data)
    }
}

function getById(toyId) {
    return axios.get(`${BASE_URL}/${toyId}`)
        .then(res => {
            console.log(res.data.toy)
            return res.data.toy
        })
}


function query(filterBy) {
    if (!filterBy) filterBy = ''
    else filterBy = `?name=${filterBy.name}&inStock=${filterBy.inStock}&type=${filterBy.type}&sortBy=${filterBy.sortBy}`;
    return axios.get(`${BASE_URL}${filterBy}`)
        .then((toys) => {
            console.log(toys.data);
            return toys.data;
        })
}

function getEmpty() {
    return {
        name: '',
        price: 0,
        type: '',
        createdAt: '',
        inStock: false,
        // _id:null
    }
}

