<template>
  <div class="toy-details">
    <h1>DETAILS</h1>
    <pre>{{toy}}</pre>
    <form v-if="currUser" @submit.prevent="addReview">
      <textarea v-model="reviewToAdd"></textarea>
    </form>
    
  </div>
</template>

<script>
// @ is an alias to /src

export default {
  name: 'ToyDetails',
  data(){
    return{
      reviewToAdd:null
    }
  },
  components: {
 
  },
  created() {
    const { toyId } = this.$route.params;
    this.$store.dispatch({ type: 'loadToy', toyId })
  },
  computed: {
    toy(){
    return this.$store.getters.currToy
    },
    currUser(){
      return this.$store.getters.currUser
    }
  },
  methods:{
    addReview(){
      console.log(this.reviewToAdd);
    }
  }
}
</script>

