<template>
  <div class="toys-filter">
    <h1>Filter</h1>
    <input type="text" v-model.lazy="filterBy.name">
    <input type="checkbox" v-model.number="filterBy.inStock">
    <select v-model="filterBy.type">
      <option value="Adult">Adult</option>
      <option value="Funny">Funny</option>
      <option value="Educational">Educational</option>
      <option value="Mashu">Mashu</option>
    </select>
    <button @click="filterBy.sortBy = 'name'">By Name</button>
    <button @click="filterBy.sortBy = 'price'">By Price</button>
    <!-- <button @click="filterList">CHECK</button> -->
  </div>
</template>

<script>
export default {
  name: "ToyFilter",
  props: ["value"],
  data() {
    return {
      filterBy: {
        name: '',
        inStock: null,
        type: '',
        sortBy: ''
      }
    };
  },
  computed: {},
  methods: {
    filterList() {
      console.log({...this.filterBy});
      this.$emit('input', {...this.filterBy});
    }
  },
  watch: {
    filterBy: {
      handler: function() {
        // console.log(this.filterBy);
        this.filterList()
      },
      deep: true
    }
  },
  created() {}
};
</script>