import axios from 'axios'

 const BASE_URL = (process.env.NODE_ENV !== 'development')  ? '/login'  : '//localhost:3003/login';



export default {
    checkUser
}


function checkUser(nickName) {
    console.log('SERVICE: ', nickName);
    return axios.put(`${BASE_URL}`, { nickName })
        .then((user) => {
            console.log(user.data);
            console.log(user);
            return (user.data) ? user.data : { nickName: 'Guest', isAdmin: false }
        })
}
