import Vue from 'vue'
import Vuex from 'vuex'
import ToyService from '../services/ToyService.js'
import UserService from '../services/UserService.js'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    toyList: [],
    currToy: null,
    filterBy: {
      name: '',
      inStock: null,
      type: '',
      sortBy: ''
    },
    userList: [],
    currUser:null
  },
  getters: {
    toyList(state) {
      return state.toyList
    },
    currToy(state) {
      return state.currToy
    },
    toyToEdit(state) {
      if (state.currToy) return JSON.parse(JSON.stringify(state.currToy))
      else return ToyService.getEmpty();
    },
    filterBy(state) {
      return state.filterBy
    },
    userList(state) {
      return state.userList
    },
    currUser(state){
      return (state.currUser) ? state.currUser : { nickName: 'Guest', isAdmin: false }
    }
  },
  mutations: {
    getToyList(state, payload) {
      state.toyList = payload.toys
      state.filterBy = payload.filterBy
    },
    setToy(state, payload) {
      state.currToy = payload.toy
    },
    removeToy(state, { toyId }) {
      var idx = state.toyList.findIndex((toy) => toy._id === toyId)
      state.toyList.splice(idx, 1)
    },
    updateToy(state, payload) {
      var idx = state.toyList.findIndex((currToy) => currToy._id === payload.toy._id)
      state.toyList.splice(idx, 1, payload.toy)
      // state.currToy = null;
      console.log('update');
    },
    addToy(state, payload) {
      state.toyList.unshift(payload.toy);
      console.log('add');
    },
    setCurrUser(state, { user }) {
      state.currUser = user
    }
  },
  actions: {
    loadToys(context) {
      return ToyService.query(context.state.filterBy)
        .then((toys) => {
          console.log(toys);
          var filterBy = context.state.filterBy
          context.commit({ type: 'getToyList', toys, filterBy })
        })
    },
    loadToy(context, { toyId }) {
      ToyService.getById(toyId)
        .then(toy => {
          context.commit({ type: 'setToy', toy })
        })
    },
    removeToy(context, { toyId }) {
      ToyService.removeToy(toyId)
        .then((res) => {
          context.commit({ type: 'removeToy', toyId })
        })
    },
    saveToy(context, { toy }) {
      const isEdit = !!toy._id;
      ToyService.saveToy(toy)
        .then((savedToy) => {
          if (isEdit) context.commit({ type: 'updateToy', toy: savedToy })
          else context.commit({ type: 'addToy', toy: savedToy })
        })
    },
    filterToys(context, { filterBy }) {
      ToyService.query(filterBy)
        .then((toys) => {
          context.commit({ type: 'getToyList', toys, filterBy })
        })
    },
    checkLogin(context, { nickName }) {
      UserService.checkUser(nickName)
        .then((user) => {
          context.commit({ type: 'setCurrUser', user })
        })
    },

  }
})
