<template>
  <div class="toy-edit">
    <h1>EDIT</h1>
    <form @submit.prevent="saveToy">
      <input type="text" v-model="toyToEdit.name">
      <input type="number" v-model="toyToEdit.price">
      <input type="text" v-model="toyToEdit.type">
      <input type="checkbox" v-model="toyToEdit.inStock">
      <button>Save</button>
    </form>
    <pre>{{toyToEdit}}</pre>
  </div>
</template>

<script>
export default {
  name: "ToyEdit",
  components: {
    
  },
  computed: {
    toyToEdit() {
      return this.$store.getters.toyToEdit;
    },
    currUser() {
      return this.$store.getters.currUser;
    }
  },
  methods: {
      saveToy() {
      this.$store.dispatch({ type: "saveToy", toy: this.toyToEdit })
      .then((result) => {
          this.$router.go(-1)
      })
    }
  },
  created() {
    const { toyId } = this.$route.params;
    console.log("CREATED", toyId);
    if (toyId) {
      this.$store.dispatch({ type: "loadToy", toyId });
    }
    if (!this.currUser.isAdmin) this.$router.go(-1)
  },
  destroyed() {
    console.log("destroyed");
    this.$store.commit({ type: "setToy", toy: {} });
  }
};
</script>

